export default function Contact({ disableImage }) {

    return (
        <section id="contacto" className='py-3 lg:py-10 bg-white'>

            <div className='container grid grid-cols-12 gap-5 lg:gap-8'>

                <form className='col-span-12 lg:col-span-6 py-5' action='https://formsubmit.co/administracion@yebala.org' method='POST' onSubmit={e => e.preventDefault}>


                    <h1 className='mb-8 font-bold text-[#3F2621] text-[40px]'>Contacto</h1>

                    <div className='flex items-center justify-between flex-wrap'>

                        <div className='w-full md:w-[49%] mb-10 md:mb-0'>

                            <label className='text-[#3F2621] font-medium'>Nombre</label>

                            <input type='text' name='name' aria-label="name" className='p-3 border-b border-b-[#D9D4D3] w-full text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40' placeholder='Su nombre' />

                        </div>

                        <div className='w-full md:w-[49%]'>

                            <label className='text-[#3F2621] font-medium'>Apellido</label>

                            <input type='text' name='last name' aria-label="last name" className='p-3 border-b border-b-[#D9D4D3] w-full text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40' placeholder='Apellido' />

                        </div>

                    </div>


                    <div className='my-10'>

                        <label className='text-[#3F2621] font-medium'>Empresa</label>

                        <input type='text' name='company' aria-label="company" className='p-3 border-b border-b-[#D9D4D3] w-full text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40' placeholder='Su nombre' />

                    </div>

                    <div className='my-10'>

                        <label className='text-[#3F2621] font-medium'>Email</label>

                        <input type='email' name='email' aria-label="email" className='p-3 border-b border-b-[#D9D4D3] w-full text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40' placeholder='Email' />

                    </div>

                    <div className='my-10'>

                        <label className='text-[#3F2621] font-medium'>Tu mensaje</label>

                        <textarea rows={4} name='message' aria-label="message" className="block resize-none mt-3 w-full border border-[#D9D4D3] !font-serif p-3 rounded-[5px] !font-normal text-[13px] placeholder:text-[#3F2621] placeholder:opacity-40" placeholder="Tipo.."></textarea>


                    </div>

                    <button className="p-6 w-full text-white text-center font-[500] text-[24px] rounded-[50px] bg-[#45B8EA]">enviar</button>


                </form>

                <div className={`col-span-12 lg:col-span-6 ${disableImage ? "hidden lg:flex" : "flex"} items-center justify-end p-5 -order-1 lg:order-[initial]`}>

                    <img src={require('./contacta.png')} className="w-full lg:w-[90%] h-full object-fill" />

                </div>

            </div>

        </section>
    )
}
